import React from 'react';
import { connect } from 'react-redux';

import Navbar from '../components/Navbar.js';




class Login extends React.Component {
    loginRequest = (e) => {
        e.preventDefault();
        let data = {
            username: document.querySelector('input#username-input').value,
            password: document.querySelector('input#password-input').value
        }

        let response1;
        fetch('http://localhost:8080/api/users/login', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }).then((data) => { 
            if(data.status === 202 && data.ok){
                this.login(data);
            }
        }).catch(error => { console.log(error) });
    }

    login = async (data) => {
        console.log(data);
        await fetch('http://localhost:8080/api/users/current').then(resp => console.log(resp.json()));
    }

    render() {
        return (
            <div className="login-wrapper">
                <Navbar></Navbar>
                <form onSubmit={this.loginRequest}>
                    <label htmlFor="username-input">username:</label>
                    <input id="username-input" placeholder="Username"></input>
                    <label htmlFor="password-input">password:</label>
                    <input id="password-input" type="password" placeholder="password"></input>
                    <button type="submit">submit</button>
                </form>
            </div>
        );
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        userLoggedIn: (user) => {
            dispatch({ type: 'LOGGING_IN', user: user });
        },
        getCurrentUser: (user) => {
            dispatch({ type: 'CURRENT_USER_DETAILS', user: user });
        }
    }
}

const mapStateToProps = (state) => {
    return {
        user: state.user
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Login);